// InputFields.js
import React from 'react';
import TextField from '@mui/material/TextField';

const InputFields = ({ initialValues, onInputChange }) => {
  const handleInputChange = (fieldName, value) => {
    const numericValue = parseFloat(value); // Convert the input value to a number
    const sanitizedValue = isNaN(numericValue) ? "" : numericValue; // Use 0 if NaN
    onInputChange(fieldName, sanitizedValue);
  };

  return (
    <div>
      <TextField
        label="Initial Amount"
        type="number"
        variant="outlined"
        value={initialValues.initialAmount}
        onChange={(e) => handleInputChange('initialAmount', e.target.value)}
        fullWidth
        margin="normal"
      />
      <TextField
        label="Monthly Deposit"
        type="number"
        variant="outlined"
        value={initialValues.monthlyDeposit}
        onChange={(e) => handleInputChange('monthlyDeposit', e.target.value)}
        fullWidth
        margin="normal"
        helperText="Enter the monthly deposit"
      />
      <TextField
        label="Interest Rate (%)"
        type="number"
        variant="outlined"
        value={initialValues.interestRate}
        onChange={(e) => handleInputChange('interestRate', e.target.value)}
        fullWidth
        margin="normal"
      />
       <TextField
        label="Target Monthly Interest"
        type="number"
        variant="outlined"
        value={initialValues.targetMonthlyInterestEarned}
        onChange={(e) => handleInputChange('targetMonthlyInterestEarned', e.target.value)}
        fullWidth
        margin="normal"
      />
    </div>
  );
};


export default InputFields;
