// ResultsTable.js
import React from 'react';
import '../../styles.css';

const ResultsTable = ({ results, themeMode}) => {
  const themeModeClass = themeMode === 'light' ? 'light-mode' : 'dark-mode';

  return (
    <table>
      <thead className={themeModeClass}>
        <tr>
          <th>Year</th>
          <th>Starting Amount</th>
          <th>Yearly Deposit</th>
          <th>Interest Rate (%)</th>
          <th>Interest Earned</th>
          <th>Monthly Interest Earned</th>
          <th>Total Amount</th>
        </tr>
      </thead>
      <tbody>
        {results.map((result, index) => (
          <tr key={index}
          className={result.highlight ? 'highlighted-row' : ''}
          >
            <td>{result.year}</td>
            <td>{result.startingAmount.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</td>
            <td>{result.monthlyDeposit.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</td>
            <td>{result.interestRate.toFixed(2)}</td>
            <td>{result.interestEarned.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</td>
            <td>{result.monthlyInterestEarned.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</td>
            <td>{result.totalAmount.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</td>
          </tr>
        ))}
      </tbody>
    </table>
  );
};


export default ResultsTable;

